import styled, { keyframes, css } from 'styled-components';
import { COLORS } from '../../common';
import { TEXT_CSS } from '../../basic/text';

// 定位呼吸灯动效
const posKeyframe = keyframes`
    0% {
      -webkit-transform: scale(0.2);
      transform: scale(0.2);
      opacity: 0.8;
    }
    80% {
      -webkit-transform: scale(1.2);
      transform: scale(1.2);
      opacity: 0;
    }
    100% {
      -webkit-transform: scale(2.2);
      transform: scale(2.2);
      opacity: 0;
    }
`;

// 定位图标
export const Pos = styled.div`
  display: flex;
  align-items: center;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  position: fixed;
  width: 24px;
  height: 24px;
  z-index: 99999;

  .circle {
    border: 2px solid #0089ff;
    width: 20px;
    height: 20px;
    margin-top: -10px;
    border-radius: 50%;
    transform: scale(1, 0.5);
    animation: ${posKeyframe} 0.8s ease-in-out infinite both;
    -webkit-animation: ${posKeyframe} 0.8s ease-in-out infinite both;
  }
`;

export const GuidanceTitle = styled.div`
  ${TEXT_CSS.H2}
  color: ${COLORS.LIGHT.LEVEL1_BASE_COLOR};
  font-weight: 500;
  display: flex;
  margin-bottom: 8px;
`;

export const GuidanceFooter = styled.div`
  display: flex;
  margin: 0px 12px 12px 12px;
  align-items: center;
  color: ${COLORS.LIGHT.LEVEL2_BASE_COLOR};
`;